import {useState} from 'react'
import {Button,Grid,TextField} from '@mui/material'

//Button
// (variant) : text, contained, outlined
// (color),
// (size)   

//Grid
//CSS Grid    
//Flexbox   Grid ,  , rowSpacing, columnSpacing
//   .
// props
//container :   
//item :   (  )
//xs(0), sm(600), md(900), lg(1200), xl(1536) :    ( 12)
// :   
//columns :   ( 12)
//rowSpacing, columnSpacing : , 

//TextField
//(input),(label),  (helperText), (error) 
//   
// props
//variant :  
//label :  
//helperText :   
//error :  true  
//fullWidth :  100% 
//multiline :   
//rows : multiline    
//type : text, password, email   

//react style   js .

//props      
const AddTodo = ({add}) => {

    const [item, setItem] = useState({title:""});

    const onIputCahnge = (e) => {
        setItem({title:e.target.value});
        console.log({title:e.target.value});
    }

    const onButtonClick = () => {
        if(item.title.trim() === ''){
            alert(' ');
            return;//   .
        }

        //props  add 
        add(item); //add({title:''})
        setItem({title:""})//     .
    }

    const enterKeyEventHandler = (e) => {
        if(e.key == "Enter"){
            onButtonClick();
        }
    }

    return(
        <Grid 
            container 
            style ={{marginTop: 20}}
            justifyContent="center"
            alignItems="center"
        >
            <Grid xs={11} md={11} item>
                <TextField 
                    placeholder='Add Todo here' 
                    fullWidth
                    value={item.title}
                    onChange={onIputCahnge} 
                    onKeyDown={enterKeyEventHandler}
                />   
            </Grid>
            <Grid xs={1} md={1} item>
                <Button 
                    fullWidth 
                    style={{height:'100%'}} 
                    color="secondary" 
                    variant='outlined'
                    onClick={onButtonClick}>
                        +
                </Button>
            </Grid>
        </Grid>
    )
}

export default AddTodo